// المتغيرات
$font-stack: Arial, sans-serif;
$main-color: #35424a;
$text-color: #333;
$background-color: #f4f4f4;
$link-hover: underline;
$card-bg: white;
$shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
$btn-delete-bg: #e74c3c;
$btn-delete-hover: #c0392b;
$btn-submit-bg: #2ecc71;
$btn-submit-hover: #c5fbdb;
$error-bg: #960f00;
$error-color: white;

// التنسيقات العامة
body {
  font-family: $font-stack;
  line-height: 1.6;
  margin: 0;
  padding: 0;
  color: $text-color;
  background-color: $background-color;
}

header {
  background: $main-color;
  color: white;
  padding: 1rem;
  text-align: center;

  nav {
    a {
      color: white;
      text-decoration: none;
      margin: 0 10px;

      &:hover {
        text-decoration: $link-hover;
      }
    }
  }
}

main {
  padding: 20px;
}

footer {
  background: $main-color;
  color: white;
  text-align: center;
  padding: 1rem;
  position: relative;
  margin-top: 2rem;
  width: 100%;
}

.experience-card {
  background: $card-bg;
  margin: 10px 0;
  padding: 15px;
  border-radius: 5px;
  box-shadow: $shadow;

  h3 {
    margin-top: 0;
    color: $main-color;
  }
}

.delete-btn {
  background: $btn-delete-bg;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 3px;
  cursor: pointer;

  &:hover {
    background: $btn-delete-hover;
  }
}

.form-group {
  margin-bottom: 15px;

  label {
    display: block;
    margin-bottom: 5px;
  }

  input,
  textarea {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }

  textarea {
    height: 100px;
  }
}

button[type="submit"] {
  background: $btn-submit-bg;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background: $btn-submit-hover;
  }
}

.error {
  background: $error-bg;
  color: $error-color;
  padding: 10px;
  margin: 10px 0;
  border-radius: 4px;
}
