body {
  color: #333;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

header {
  color: #fff;
  text-align: center;
  background: #35424a;
  padding: 1rem;
}

header nav a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

header nav a:hover {
  text-decoration: underline;
}

main {
  padding: 20px;
}

footer {
  color: #fff;
  text-align: center;
  background: #35424a;
  width: 100%;
  margin-top: 2rem;
  padding: 1rem;
  position: relative;
}

.experience-card {
  background: #fff;
  border-radius: 5px;
  margin: 10px 0;
  padding: 15px;
  box-shadow: 0 2px 5px #0000001a;
}

.experience-card h3 {
  color: #35424a;
  margin-top: 0;
}

.delete-btn {
  color: #fff;
  cursor: pointer;
  background: #e74c3c;
  border: none;
  border-radius: 3px;
  padding: 5px 10px;
}

.delete-btn:hover {
  background: #c0392b;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  margin-bottom: 5px;
  display: block;
}

.form-group input, .form-group textarea {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  padding: 8px;
}

.form-group textarea {
  height: 100px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background: #2ecc71;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
}

button[type="submit"]:hover {
  background: #c5fbdb;
}

.error {
  color: #fff;
  background: #960f00;
  border-radius: 4px;
  margin: 10px 0;
  padding: 10px;
}
